.Card {
  color: var(--text);
  background-color: white;
  border: 4px solid var(--secondary);
  border-radius: 0.5rem;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  padding: calc(0.4rem + 0.2vw);
  position: relative;
  cursor: pointer;
  transition: all 0.4 ease;
}

.Card::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 0.3rem;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  opacity: 0;
  transition: all 0.4s ease;
  z-index: -1;
}

.Card:hover::before {
  opacity: 1;
}

.Card:hover {
  box-shadow: -2px -2px 5px var(--secondary);
}

.CardImg {
  width: 100%;
  aspect-ratio: calc(109/188);
  border-radius: 0.3rem;
}

h2 {
  font-size: clamp(0.5rem, 0.5rem + 0.7vw, 1rem);
  margin: 0.5rem 0 0;
}

@media screen and (max-width: 480px) {
  h2 {
    font-size: 0.8rem;
  }
}