header {
  color: white;
  background-color: var(--primary);
  height: 5rem;
  padding: 0 8vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.HeaderTitle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.Logo {
  height: 3.8rem;
}

h1 {
  font-size: 1.8rem;
  margin: 0;
}

.Scores {
  display: flex;
  gap: 3vw;
}

.ScoreCard {
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
}

.ScoreCard > span {
  font-size: 1.4rem;
}

@media screen and (max-width: 480px) {
  header {
    padding: 0 5vw;
  }

  .Logo {
    height: 3rem;
  }

  h1 {
    font-size: 1.6rem;
  }

  .Scores {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .ScoreCard {
    font-size: 1.1rem;
    flex-direction: row;
    gap: 0.75rem;
  }

  .ScoreCard > span {
    font-size: 1.3rem;
  }
}
