.Modal {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.Instructions {
  background-color: white;
  margin: 15% auto;
  padding: 1.5rem;
  border-radius: 0.75rem;
  width: min(75%, 40rem);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  position: relative;
  animation-name: fromtop;
  animation-duration: 0.5s
}

@keyframes fromtop {
  from {top: -15rem; opacity: 0}
  to {top: 0; opacity: 1}
}

h3 {
  color: var(--primary);
  font-size: clamp(1rem, 1rem + 1vw, 1.8rem);
  margin: 0;
}

p {
  color: var(--text);
  font-size: clamp(0.8rem, 0.8rem + 0.5vw, 1rem);
  line-height: 1.25rem;
}

.Start {
  color: #aaa;
  font-size: clamp(1rem, 1rem + 0.7vw, 1.5rem);
  padding: 0.5rem 1rem;
  transition: all 0.2s ease;
}

.Start:hover,
.Start:focus {
  color: var(--text);
  text-decoration: none;
  cursor: pointer;
}
